@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body,html{
    font-family: 'Roboto', sans-serif!important;
    
}

:root{
    --primaryColor:#f6c33c;
    --secondryColor:#561d00;
    --thirdColor:#222222;
    --hoverColor:#dca000;
    --first:#FFCE45;
    --second:#D4AC2B;
    --third:#B05E27;
    --fourth:#7E370C;

}

*{
    box-sizing: border-box;
}


p{
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5rem;
}

/*Navbar*/


.section_navbar{
    width: 100%;
    height: auto;
    position: relative;
}

.navbar-brand {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.navbar-brand img{
    width: 98px;
    height: auto;

}

.navbar-brand h6{
    font-size: 14px;
}

.nav-link:focus, .nav-link:hover {
    color: #dca000!important;
    color: var(--hoverColor)!important;
}


.navbar-nav .nav-item {
    padding: 0 0 0 21px;
}
.navbar-toggler {
     border: var(--bs-border-width) solid #f6c33c!important;
    border-radius: var(--bs-navbar-toggler-border-radius);
   
}

.nav-link {
    font-size: 15px!important;
    font-weight: 400!important;
    color: #000!important;
 }


 .navbar-nav .nav-link.active, .navbar-nav .show>.nav-link {
    color: #dca000!important;
    color: var(--hoverColor)!important;
}


.topnav{
    background-color: #222222;
    background-color: var(--thirdColor);
    color: #f6c33c;
    color: var(--primaryColor);
    width: 38%;
    height: auto;
    padding: 6px;
    position: absolute;
    right: 0;
    top: 0;
    font-size: 14px;
    font-weight: 400;
    z-index: 9;
  
    
}

.topnav::before{
  content: "";
   width: 0; 
   height: 0; 
   border-top: 37px solid #222222; 
   border-top: 37px solid var(--thirdColor); 
   border-left: 18px solid transparent;
   position: absolute;
   left: -18px;
   top: 0;
}

.top_inner{
    display: flex;
    width: 90%;
    margin: 0 auto;
    justify-content: space-between;
}

.top_inner p span{
    margin-right: 5px;
}
.top_inner a{
    color: #f6c33c;
    color: var(--primaryColor); 
}
.divider_br{
    width: 2px;
    height: 14px;
    background-color: #f6c33c;
    background-color: var(--primaryColor);
    display: flex;
    align-self: center;
}

.topnav p{
    padding: 0;
    margin: 0;
    font-size: 14px;
   

}


/**************** Hero ***********************/
.hero{
    width: 100%;
    height: auto;
    padding: 0;
    background-color: #e3e2e2;
  
}

/************** About Section *****************/

.about_section{
  width: 100%;
  height: auto;
}
.about_section h1{
    font-size: 26px;
    color: #000;
    font-weight: 500;
    text-transform: uppercase;
    position: relative;
    line-height: 55px;
  
}

.about_section h1::after{
    content: "";
    width: 17%;
    height: 1px;
    background-color: #dca000;
    background-color: var(--hoverColor);
    margin: 0 auto;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
}

.about_inner{
    width: 100%;
    height: 56vh;
    background-image: url(/shiv_travels/static/media/about_img1.323b0426.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    display: flex;
    align-items: center;
    justify-content: center;
}

.about_dic{
    width: 71%;
    height: auto;
    background-color: #fff;
    border-radius: 10px;
    padding: 16px;
    

}



.about_dic h3{
    font-size: 20px;
    color: #000;
    font-weight: 500;
   
}

.about_dic p{
    font-size: 1rem;
    font-weight: 400;
    color: #585858;

}


/**************** Services *******************/

.services{
    width: 100%;
    height: auto;
    
}
.services h2{
    font-size: 26px;
    color: #000;
    font-weight: 500;
    text-transform: uppercase;
    position: relative;
    line-height: 55px;
}

.services h2::after{
    content: "";
    width: 14%;
    height: 1px;
    background-color: #dca000;
    background-color: var(--hoverColor);
    margin: 0 auto;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
}

.bgservic_card{
    width: 100%;
    height: 290px;
     transition: 0.3s;
    position: relative;
}
.bgservic_card img{
    width: 100%;
    height: 100%;
    border-radius: 10px;
   
}

.bgservic_card:hover,.bg_opa:hover{
   background-color: transparent;
   cursor: pointer;

}
.bgservic_card:hover h3{
    color: #fff;
    background-color: #D4AC2B;
    padding: 12px;
    border-radius: 7px;
}


.bg_opa{
    width: 100%;
    height: 100%;
    padding: 0;
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: 10px;
    transition: 0.3s;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 999;
    position: absolute;
    top: 0;
    left: 0;
}

.bg_opa h3{
  font-size: 14px;
  font-weight: 400;
  text-transform: uppercase;
  color: #fff;
  background-color: transparent;
  transition: 0.3s;
  margin: 0;
  padding: 0;
}


/***********************  **************************/

.clients{
    width: 100%;
    height: auto;
}


.clients h4{
    font-size: 26px;
    color: #000;
    font-weight: 500;
    text-transform: uppercase;
    position: relative;
    line-height: 55px;
}
.clients h4::after {
    content: "";
    width: 14%;
    height: 1px;
    background-color: #dca000;
    background-color: var(--hoverColor);
    margin: 0 auto;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
}


.wrap{
    overflow: hidden;
}

  .thumb_slider {
    -webkit-animation: slidein 30s linear infinite;
            animation: slidein 30s linear infinite;
    white-space: nowrap;

  }

  .logos {
    width: 21%;
    display: inline-flex;
    margin: 0px 0;

  }
  .box {
    width: 100%;
    height: auto;
    -webkit-animation: fade-in 0.5s 
      cubic-bezier(0.455, 0.03, 0.515, 0.955) forwards;
            animation: fade-in 0.5s 
      cubic-bezier(0.455, 0.03, 0.515, 0.955) forwards;
      margin-right: 5px;
  }





  @-webkit-keyframes slidein {
    from {
      -webkit-transform: translate3d(0, 0, 0);
              transform: translate3d(0, 0, 0);
    }
    to {
      -webkit-transform: translate3d(-100%, 0, 0);
              transform: translate3d(-100%, 0, 0);
    }
  }





  @keyframes slidein {
    from {
      -webkit-transform: translate3d(0, 0, 0);
              transform: translate3d(0, 0, 0);
    }
    to {
      -webkit-transform: translate3d(-100%, 0, 0);
              transform: translate3d(-100%, 0, 0);
    }
  }
  
  @-webkit-keyframes fade-in {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  
  @keyframes fade-in {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }



/******************** Footer *********************/
