/********************** MEDIA QUERY HERE ****************************/
/* Small devices (landscape phones, 576px and up)*/
@media (min-width: 320px) and (max-width: 480px){ 
    .topnav {
        background-color: #222222;
        background-color: var(--thirdColor);
        color: #f6c33c;
        color: var(--primaryColor);
        width: 100%;
        height: auto;
        padding: 6px;
        position: relative;
        top: 0;
        left: 0;
        font-size: 14px;
        font-weight: 400;
    }

    .top_inner {
        display: flex;
        width: 100%;
        margin:0;
        justify-content: center;
        flex-direction: column;
        align-items: center;
    }

    .about_section h1 {
        font-size: 24px;
        line-height: 36px;
    }


    .about_dic {
        width: 87%;
        height: auto;
        background-color: #fff;
        border-radius: 10px;
        padding: 10px;

    }

    .about_inner {
      height: auto;
      background-size: cover;

    }
    .services h2 {
        font-size: 24px;
        line-height: 36px;
    }

    .clients h4 {
        font-size: 24px;
        line-height: 36px;
    }
    .box {
        width: 100%;
        height: auto;
        -webkit-animation: fade-in 0.5s cubic-bezier(0.455, 0.03, 0.515, 0.955) forwards;
                animation: fade-in 0.5s cubic-bezier(0.455, 0.03, 0.515, 0.955) forwards;
        margin-right: 0px;
    }

    .logos {
        width: 82%;
        display: inline-flex;
        margin: 0px 0;
    }

    .satis_inner{
        flex-direction: column;
        text-align: center;
    }

    .topCardservices h4 {
       line-height: 36px;
    }


}

/* Medium devices (tablets, 768px and up)*/
@media (min-width: 481px) and (max-width: 768px){ 

    .topnav {
        background-color: #222222;
        background-color: var(--thirdColor);
        color: #f6c33c;
        color: var(--primaryColor);
        width: 100%;
        height: auto;
        padding: 6px;
        position: relative;
        top: 0;
        left: 0;
        font-size: 14px;
        font-weight: 400;
    }

    .top_inner {
        display: flex;
        width: 100%;
        margin:0;
        justify-content: center;
        flex-direction: column;
        align-items: center;
    }

    .about_section h1 {
        font-size: 24px;
        line-height: 36px;
    }


    .about_dic {
        width: 87%;
        height: auto;
        background-color: #fff;
        border-radius: 10px;
        padding: 10px;

    }
    .box {
        width: 100%;
        height: auto;
        -webkit-animation: fade-in 0.5s cubic-bezier(0.455, 0.03, 0.515, 0.955) forwards;
                animation: fade-in 0.5s cubic-bezier(0.455, 0.03, 0.515, 0.955) forwards;
        margin-right: 0px;
    }

    .logos {
        width: 53%;
        display: inline-flex;
        margin: 0px 0;
    }

}






/* Landscape */
@media only screen 
  and (min-device-width: 768px) 
  and (max-device-width: 1024px) 
  and (orientation: landscape) 
  and (-webkit-min-device-pixel-ratio: 2) {

    .topnav{
        width: 46%;
    }
    .top_inner{
        width: 92%;
    }
    .logos {
        width: 26%;
      
    }

}


/* Ipad air*/
/* Portrait */
@media only screen 
  and (min-device-width: 768px) 
  and (max-device-width: 1024px) 
  and (orientation: portrait) 
  and (-webkit-min-device-pixel-ratio: 1) {

.topnav {
    width: 55%;
}
.top_inner{
    width: 100%;
}

.logos {
    width: 36%;
  
}
 }





